import classes from './Products.module.css'
import { Fragment,  useContext,useState} from 'react';
import CartContext from '../store/cart-context';
import { NavLink } from 'react-router-dom';
// import Info from '../Information/info';

const Products =(props)=>{
const cartCtx = useContext(CartContext)
  
  localStorage.setItem('itemsTotal',JSON.stringify(cartCtx.totalAmount))
  const [touched, settouched] = useState(true)
  
  const addItemToCartHandler = ((item) => {
    item.preventDefault()
    cartCtx.addItem(
      {
        id:(props.id),
        Name:props.Name,
        AirDate:props.airdate,
        EndDate:props.EndDate,
        Image:props.thumbnail,
        genres:props.genres,
        amount:cartCtx.addItem.length,
        totalAmount:cartCtx.totalAmount
      })
      
      return settouched(false)
    })

 

  return (
        <Fragment>
        <div  className={classes.products}>
          <h2>{props.Name}</h2>
        <img src={props.thumbnail}  alt="Avatar" style={{'width':'100%'}} />
        <div className={classes.container}>
          <h4>
            <b> On Air: {props.airdate}</b>
            <b> EndDate: {props.EndDate}</b>
          </h4>
          <p> Genres: { props.genres[0]} , {props.genres[1]} ,{props.genres[2]} </p>
       {touched && <button onClick={addItemToCartHandler} >Add To Favorites</button>}
       {!touched  && <p>Already In Cart</p>}
        </div>
        <NavLink to ={`/Shopping-Cart/Info/${props.id}`}>
          View More
        </NavLink>
      </div>
      </Fragment>

    )
};

export default Products;