import classes from './infoshow.module.css';
  import React from 'react'
  
  const Infoshow = (props) => {
      return (
        <div className={classes.info}>
  <div>
      <img  src ={props.Image} alt="No"  />
        <center>
        </center>    
</div>
 <div> 

<h1>{props.name}</h1> 
<h4> Rating:  {props.Rating} </h4>
<p> { props.summary} </p>



</div>

            
        </div>
      )
  }
  
  export default Infoshow
  



 

 